"use client"

import { useState, useRef, useEffect } from "react"
import { useRouter } from "next/navigation"
import Script from "next/script"

export default function WufooFormSection() {
  const router = useRouter()
  const [formData, setFormData] = useState({
    name: "",
    phone: "",
    email: "",
    message: "",
  })
  const [file, setFile] = useState<File | null>(null)
  const [isSubmitting, setIsSubmitting] = useState(false)
  const fileInputRef = useRef<HTMLInputElement>(null)
  const formRef = useRef<HTMLFormElement>(null)

  useEffect(() => {
    // Initialize form focus handlers after component mounts
    if (typeof window !== 'undefined' && formRef.current) {
      const form = formRef.current
      const fields = form.querySelectorAll('input, textarea, select')
      
      fields.forEach((field) => {
        field.addEventListener('focus', function() {
          const li = this.closest('li')
          if (li) {
            // Remove focused class from all other fields
            form.querySelectorAll('li.focused').forEach((focusedLi) => {
              if (focusedLi !== li) {
                focusedLi.classList.remove('focused')
              }
            })
            // Add focused class to current field
            li.classList.add('focused')
          }
        })
        
        field.addEventListener('blur', function() {
          const li = this.closest('li')
          if (li) {
            li.classList.remove('focused')
          }
        })
      })
    }
  }, [])

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files[0]) {
      setFile(e.target.files[0])
    }
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsSubmitting(true)
    
    // Simulate API call
    await new Promise(resolve => setTimeout(resolve, 1000))
    
    setIsSubmitting(false)
    // Reset form
    setFormData({ name: "", phone: "", email: "", message: "" })
    setFile(null)
    if (fileInputRef.current) {
      fileInputRef.current.value = ""
    }
    
    // Navigate to thank you page
    router.push("/thank-you")
  }

  return (
    <>
      <section className="py-8" style={{ background: '#eee', minHeight: '100vh' }}>
        <div id="container" style={{ 
          textAlign: 'left', 
          background: '#fff', 
          border: '1px solid #ccc',
          boxShadow: 'rgba(0,0,0,0.2) 0px 0px 5px',
          margin: '0 auto 10px auto',
          width: '640px',
          maxWidth: '100%',
          padding: '0'
        }}>
          {/* Logo */}
          <div id="logo" style={{
            margin: 0,
            padding: 0,
            minHeight: 0,
            backgroundColor: '#dedede',
            textIndent: '-9000px',
            textDecoration: 'none'
          }}>
            <a href="#" style={{
              minHeight: '40px',
              height: '40px',
              display: 'block',
              background: 'url(/images/wflogo.png) no-repeat left top',
              overflow: 'hidden'
            }}>Wufoo</a>
          </div>

          {/* Form */}
          <form 
            ref={formRef}
            className="wufoo" 
            onSubmit={handleSubmit}
            style={{
              margin: '20px 14px 0 14px',
              padding: '0 0 20px 0',
              position: 'relative'
            }}
          >
            {/* Info Section */}
            <div className="info" style={{
              display: 'inline-block',
              clear: 'both',
              margin: '0 0 5px 0',
              padding: '0 1% 1.1em 1%',
              borderBottom: '1px dotted #ccc'
            }}>
              <h2 style={{
                fontWeight: 'normal',
                fontSize: '160%',
                margin: '0 0 5px 0',
                clear: 'left'
              }}>Contact Us</h2>
            </div>

            <ul style={{ margin: 0, padding: 0, listStyleType: 'none' }}>
              {/* Name Field */}
              <li style={{ margin: 0, padding: '6px 1% 9px 1%', clear: 'both', backgroundColor: 'transparent', position: 'relative' }}>
                <label className="desc" htmlFor="Field1" style={{
                  fontSize: '95%',
                  fontWeight: 'bold',
                  color: '#222',
                  lineHeight: '150%',
                  margin: 0,
                  padding: '0 0 3px 0',
                  border: 'none',
                  display: 'block',
                  whiteSpace: 'normal',
                  width: '100%'
                }}>
                  Your Name
                  <span className="req" style={{ display: 'inline', float: 'none', color: 'red', fontWeight: 'bold', margin: 0, padding: 0 }}>*</span>
                </label>
                <div>
                  <input
                    className="field text medium"
                    id="Field1"
                    name="name"
                    type="text"
                    value={formData.name}
                    onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                    required
                    style={{
                      fontFamily: '"Lucida Grande", Tahoma, Arial, sans-serif',
                      fontSize: '100%',
                      color: '#333',
                      margin: 0,
                      padding: '2px 0',
                      borderTop: '1px solid #7c7c7c',
                      borderLeft: '1px solid #c3c3c3',
                      borderRight: '1px solid #c3c3c3',
                      borderBottom: '1px solid #ddd',
                      background: '#fff url(/images/fieldbg.gif) repeat-x top',
                      width: '100%'
                    }}
                  />
                </div>
              </li>

              {/* Phone Field */}
              <li style={{ margin: 0, padding: '6px 1% 9px 1%', clear: 'both', backgroundColor: 'transparent', position: 'relative' }}>
                <label className="desc" htmlFor="Field2" style={{
                  fontSize: '95%',
                  fontWeight: 'bold',
                  color: '#222',
                  lineHeight: '150%',
                  margin: 0,
                  padding: '0 0 3px 0',
                  border: 'none',
                  display: 'block',
                  whiteSpace: 'normal',
                  width: '100%'
                }}>
                  Your Phone
                </label>
                <div>
                  <input
                    className="field text medium"
                    id="Field2"
                    name="phone"
                    type="tel"
                    value={formData.phone}
                    onChange={(e) => setFormData({ ...formData, phone: e.target.value })}
                    style={{
                      fontFamily: '"Lucida Grande", Tahoma, Arial, sans-serif',
                      fontSize: '100%',
                      color: '#333',
                      margin: 0,
                      padding: '2px 0',
                      borderTop: '1px solid #7c7c7c',
                      borderLeft: '1px solid #c3c3c3',
                      borderRight: '1px solid #c3c3c3',
                      borderBottom: '1px solid #ddd',
                      background: '#fff url(/images/fieldbg.gif) repeat-x top',
                      width: '100%'
                    }}
                  />
                </div>
              </li>

              {/* Email Field */}
              <li style={{ margin: 0, padding: '6px 1% 9px 1%', clear: 'both', backgroundColor: 'transparent', position: 'relative' }}>
                <label className="desc" htmlFor="Field3" style={{
                  fontSize: '95%',
                  fontWeight: 'bold',
                  color: '#222',
                  lineHeight: '150%',
                  margin: 0,
                  padding: '0 0 3px 0',
                  border: 'none',
                  display: 'block',
                  whiteSpace: 'normal',
                  width: '100%'
                }}>
                  Your Email
                  <span className="req" style={{ display: 'inline', float: 'none', color: 'red', fontWeight: 'bold', margin: 0, padding: 0 }}>*</span>
                </label>
                <div>
                  <input
                    className="field text medium"
                    id="Field3"
                    name="email"
                    type="email"
                    value={formData.email}
                    onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                    required
                    style={{
                      fontFamily: '"Lucida Grande", Tahoma, Arial, sans-serif',
                      fontSize: '100%',
                      color: '#333',
                      margin: 0,
                      padding: '2px 0',
                      borderTop: '1px solid #7c7c7c',
                      borderLeft: '1px solid #c3c3c3',
                      borderRight: '1px solid #c3c3c3',
                      borderBottom: '1px solid #ddd',
                      background: '#fff url(/images/fieldbg.gif) repeat-x top',
                      width: '100%'
                    }}
                  />
                </div>
              </li>

              {/* Message Field */}
              <li style={{ margin: 0, padding: '6px 1% 9px 1%', clear: 'both', backgroundColor: 'transparent', position: 'relative' }}>
                <label className="desc" htmlFor="Field4" style={{
                  fontSize: '95%',
                  fontWeight: 'bold',
                  color: '#222',
                  lineHeight: '150%',
                  margin: 0,
                  padding: '0 0 3px 0',
                  border: 'none',
                  display: 'block',
                  whiteSpace: 'normal',
                  width: '100%'
                }}>
                  Your Message
                  <span className="req" style={{ display: 'inline', float: 'none', color: 'red', fontWeight: 'bold', margin: 0, padding: 0 }}>*</span>
                </label>
                <div>
                  <textarea
                    className="field textarea medium"
                    id="Field4"
                    name="message"
                    rows={5}
                    value={formData.message}
                    onChange={(e) => setFormData({ ...formData, message: e.target.value })}
                    required
                    style={{
                      fontFamily: '"Lucida Grande", Tahoma, Arial, sans-serif',
                      fontSize: '100%',
                      color: '#333',
                      margin: 0,
                      padding: '2px 0',
                      borderTop: '1px solid #7c7c7c',
                      borderLeft: '1px solid #c3c3c3',
                      borderRight: '1px solid #c3c3c3',
                      borderBottom: '1px solid #ddd',
                      background: '#fff url(/images/fieldbg.gif) repeat-x top',
                      width: '100%',
                      minWidth: '100%',
                      maxWidth: '100%',
                      resize: 'vertical'
                    }}
                  />
                </div>
              </li>

              {/* File Upload Field */}
              <li className="file" style={{ margin: 0, padding: '6px 1% 9px 1%', clear: 'both', backgroundColor: 'transparent', position: 'relative' }}>
                <label className="desc" htmlFor="Field5" style={{
                  fontSize: '95%',
                  fontWeight: 'bold',
                  color: '#222',
                  lineHeight: '150%',
                  margin: 0,
                  padding: '0 0 3px 0',
                  border: 'none',
                  display: 'block',
                  whiteSpace: 'normal',
                  width: '100%'
                }}>
                  Attach a File
                </label>
                <div style={{ margin: 0, padding: 0, color: '#444' }}>
                  <input
                    ref={fileInputRef}
                    className="field file"
                    id="Field5"
                    name="file"
                    type="file"
                    onChange={handleFileChange}
                    style={{
                      fontFamily: '"Lucida Grande", Tahoma, Arial, sans-serif',
                      fontSize: '100%',
                      color: '#333',
                      margin: 0,
                      padding: '2px 0',
                      width: 'auto'
                    }}
                  />
                  <span style={{ display: 'inline-block', marginLeft: '10px', color: '#666', fontSize: '85%' }}>
                    {file ? file.name : ''}
                  </span>
                </div>
              </li>

              {/* Submit Button */}
              <li className="buttons" style={{
                width: 'auto !important',
                position: 'relative',
                clear: 'both',
                padding: '10px 1% 10px 1%'
              }}>
                <div>
                  <input
                    id="saveForm"
                    name="saveForm"
                    className="btTxt submit"
                    type="submit"
                    value="Submit"
                    disabled={isSubmitting}
                    style={{
                      fontSize: '100%',
                      marginRight: '5px',
                      padding: '0 7px',
                      width: 'auto',
                      overflow: 'visible',
                      cursor: isSubmitting ? 'not-allowed' : 'pointer',
                      opacity: isSubmitting ? 0.6 : 1
                    }}
                  />
                </div>
              </li>
            </ul>
          </form>

          {/* Footer */}
          <div style={{ textAlign: 'center', padding: '20px', color: '#666' }}>
            <div style={{ marginBottom: '10px' }}>
              <img src="/images/powerlogo.png" alt="Wufoo" style={{ border: 'none' }} />
            </div>
            <div style={{ fontSize: '85%' }}>Designed</div>
          </div>
        </div>
      </section>
      
      {/* Load Wufoo JavaScript */}
      <Script src="/scripts/wufoo.js" strategy="lazyOnload" />
    </>
  )
}

